.columns {
  color: #ffffff;
  background-color: #008662;
  height: 44.88px;
  font-weight: 300;
  font-size: 12px;
  text-transform: uppercase;
  text-indent: 25px;
  position: relative;
}

.columns:first-child {
  border-top-left-radius: 6px;
}

.columns:last-child {
  border-top-right-radius: 6px;
}

.columns span {
  cursor: pointer;
}

.filterIcons,
.clearFilterIcons {
  position: relative;
  margin-bottom: 2px;
  cursor: pointer;
}

.clearFilterIcons {
  filter: brightness(0) invert(1);
  width: 12.46px;
  height: 15px;
  margin-left: 10px;
}

.dropdownFilter,
.dropdownDateCreated,
.dropdownValidityDate,
.dropdownTitle {
  position: absolute;
  background-color: #ffffff;
  color: #000000;
  bottom: 0;
  margin-left: 25px;
  border-radius: 4px;
  border: 1px solid #b1b1b1;
  z-index: 1;
  box-shadow: 0px 3px 5px #c5c5c5;
}

.dropdownFilter {
  width: 232px;
  height: 180px;
  margin-bottom: -182px;
}

.dropdownDateCreated {
  right: 0;
  margin-right: 0px;
  width: 850px;
  margin-bottom: -300px;
  padding-top: 10px;
  padding-bottom: 20px;
  top: 46px;
}

.dropdownValidityDate {
  margin-right: 0px;
  width: 850px;
  margin-bottom: -300px;
  padding-top: 10px;
  padding-bottom: 20px;
  top: 46px;
}

.dropdownTitle {
  width: 175px;
  height: 165px;
  margin-bottom: -167px;
}
