.react-calendar__navigation__label,
.react-calendar__month-view__weekdays__weekday,
.react-calendar__month-view__days__day {
  background-color: transparent;
  border: none;
}

.react-calendar__navigation__prev2-button,
.react-calendar__navigation__next2-button {
  display: none;
}

.react-calendar__navigation__prev-button {
  visibility: hidden;
  margin-left: 15px;
}
.react-calendar__navigation__prev-button:after {
  visibility: visible;
  content: url('../../../../../assets/icons/Chev-Left-Bold.png');
}

.react-calendar__navigation__next-button {
  visibility: hidden;
  margin-right: 15px;
}
.react-calendar__navigation__next-button:after {
  visibility: visible;
  content: url('../../../../../assets/icons/Chev-Right-Bold.png');
}

.react-calendar__navigation__prev-button,
.react-calendar__navigation__next-button {
  border: none;
  background-color: transparent;
}

.react-calendar__month-view__weekdays__weekday,
.react-calendar__month-view__days__day {
  width: 21px;
  height: 21px;
}

.react-calendar__navigation__label {
  font-weight: 300;
  font-size: 12px;
  line-height: 14px;
  color: #000000;
}

.react-calendar__month-view__weekdays__weekday {
  margin-top: 15px;
  font-weight: 300;
  font-size: 10px;
  line-height: 12px;
  color: #787e82;
}

.react-calendar__month-view__weekdays__weekday:nth-child(1) {
  margin-left: -10px;
}

.react-calendar__month-view__days__day {
  font-weight: 300;
  font-size: 10px;
  line-height: 12px;
  text-align: center;
  color: #000000;
}

.react-calendar__month-view__days__day:hover {
  background-color: #e5e5e5;
}

.react-calendar__year-view__months__month,
.react-calendar__decade-view__years__year,
.react-calendar__century-view__decades__decade {
  background-color: transparent;
  border: none;
  height: 30px;
}

.react-calendar__year-view__months__month:hover,
.react-calendar__decade-view__years__year:hover,
.react-calendar__century-view__decades__decade:hover {
  background-color: #e5e5e5;
}

.react-calendar__year-view__months__month:nth-child(1),
.react-calendar__year-view__months__month:nth-child(2),
.react-calendar__year-view__months__month:nth-child(3),
.react-calendar__decade-view__years__year:nth-child(1),
.react-calendar__decade-view__years__year:nth-child(2),
.react-calendar__decade-view__years__year:nth-child(3),
.react-calendar__century-view__decades__decade:nth-child(1),
.react-calendar__century-view__decades__decade:nth-child(2),
.react-calendar__century-view__decades__decade:nth-child(3) {
  margin-top: 10px;
}

.react-calendar__tile--active {
  background-color: #008662;
  color: #ffffff;
}

.react-calendar__month-view__days__day--neighboringMonth {
  visibility: hidden;
}

.react-datepicker__navigation-icon {
  top: 5px;
}
