.options {
  width: 170px;
  height: 100%;
  border-right: 2px solid #e6edf3;
}

.optionItem:nth-child(1) {
  margin-top: -10px;
}

.optionItem {
  height: 42px;
  font-size: 12px;
  font-weight: 600;
  padding-top: 15px;
  padding-bottom: 15px;
  cursor: pointer;
  text-transform: capitalize;
}

.activeItem {
  background-color: #116c53;
  color: #ffffff;
  font-weight: 400;
}

.dateSection {
  width: 88%;
  height: 100%;
  padding-right: 20px;
}

.startSection,
.endSection {
  width: 50%;
  padding: 5px;
}

.divider {
  margin-top: 30px;
  width: 1px;
  height: 150px;
  border-right: 1px solid #e6edf3;
}

.dateRangeDiv {
  width: 100%;
  text-align: center;
}

.dateRangeString {
  margin-left: -10px;
  font-weight: 300;
  font-size: 14px;
  line-height: 17px;
  color: #000000;
}

.totalDays {
  width: 100%;
  text-align: center;
  font-weight: 100;
  font-size: 12px;
  line-height: 14px;
  color: #808080;
  margin-bottom: 10px;
  margin-left: -12px;
}

.arrowLeftIcon {
  width: 25px;
  height: 15px;
  margin-left: 105px;
  margin-right: 120px;
}

.clearFilterButton {
  color: #6d6d73;
  font-size: 12px;
  line-height: 14px;
  cursor: pointer;
  margin-left: 42px;
  margin-top: 11px;
  text-transform: capitalize;
}

.applyFilterButton {
  width: 77px;
  height: 36px;
  background: #008662;
  border-radius: 4px;
  /* padding: 11px 24px; */
  border: none;
  color: #ffffff;
  margin-left: 20px;
}
