.startsWithButton {
  position: relative;
  width: 198px;
  height: 32px;
  margin-left: -25px;
  border: none;
  color: #ffffff;
  background: #008662;
  border-radius: 4px;
  text-align: left;
  padding-left: 15px;
  padding-right: 15px;
}

.startsWithButton:after {
  position: absolute;
  right: 0;
  margin-right: 15px;
  content: url('../../../../../assets/icons/Triangle-Down.png');
}

.startsWithInput {
  width: 198px;
  height: 32px;
  padding: 5px 15px;
  border-radius: 4px;
  border: 1px solid #ced4da;
  color: #6d6d73;
}

.startsWithInput::placeholder {
  color: #b1b1b1;
}

.startsWithInput:focus {
  outline: none;
}

.clearFilterButton {
  color: #6d6d73;
  font-size: 12px;
  line-height: 14px;
  cursor: pointer;
  margin-left: 42px;
  margin-top: 11px;
  text-transform: capitalize;
}

.filterButton {
  width: 77px;
  height: 36px;
  background: #008662;
  border-radius: 4px;
  /* padding: 11px 24px; */
  border: none;
  color: #ffffff;
  margin-left: 20px;
}

.filterButton:disabled {
  opacity: 0.7;
}

.options {
  position: absolute;
  bottom: 0;
  left: 0;
  margin-bottom: -128px;
  height: 130px;
  width: 100%;
  background-color: #ffffff;
  border: 1px solid #b1b1b1;
  z-index: 2;
}

.optionItem {
  height: 25px;
  width: 100%;
  color: #000000;
  background-color: #ffffff;
  font-size: 12px;
  padding-top: 6.5px;
  padding-bottom: 6.5px;
  cursor: pointer;
  text-transform: capitalize;
}

.optionItem:hover {
  background-color: #ced4da;
}

.optionItem span {
  margin-left: -15px;
}
