.table {
  width: 100%;
  background-color: #ffffff;
}

.boxShadow {
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
}

.tableBodyDiv {
  overflow-x: auto;
  overflow-y: auto;
  height: auto;
  /* max-height: calc(100vh - 250px); */
  height: calc(100vh - 250px);
}

/* @media screen and (max-height: 900px) {
  .tableBodyDiv {
    height: 660px;
  }
} */

.table,
thead {
  display: table;
  width: 100%;
  table-layout: fixed;
}

.tableBody {
  display: table;
  width: 100%;
  table-layout: fixed;
}

.activeView {
  min-width: 50px !important;
  width: 50px !important;
  border-right: 1px solid white;
}

/* width */
.tableBodyDiv::-webkit-scrollbar {
  width: 5px !important;
}

/* Track */
.tableBodyDiv::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey !important; 
  border-radius: 10px !important;
}
 
/* Handle */
.tableBodyDiv::-webkit-scrollbar-thumb {
  background: #787e82  !important; 
  border-radius: 10px !important;
}

/* Handle on hover */
.tableBodyDiv::-webkit-scrollbar-thumb:hover {
  background: #fff !important; 
}
.table-body-row{
  width:100% !important;
}
.td{
  width: 'inherit';
}